import { create } from "zustand";

import { persist } from "zustand/middleware";

const useAuthStore = create(
  persist(
    (set) => ({
      user: null, // El estado inicial del usuario es null

      login: (userData) => set({ user: userData }), // Función para loguear al usuario

      logout: () => set({ user: null }), // Función para desloguear al usuario
    }),
    {
      name: "auth", // nombre del item en el sessionStorage
      getStorage: () => sessionStorage, // especifica que se debe utilizar sessionStorage
    }
  )
);

export default useAuthStore;
