import { Link } from "react-router-dom"; // Asegúrate de tener react-router-dom instalado
import UserProfile from "./UserProfile";
import { useTranslation } from "react-i18next";
import LangSelector from "./LangSelector";

const Navbar = () => {
  const { t } = useTranslation();
  return (
    <nav className="bg-gray-800 text-white p-4">
      <div className="container mx-auto flex justify-between items-center">
        <Link to="/">
          <h1 className="text-lg font-bold">{t("app.name")}</h1>
        </Link>
        <ul className="flex space-x-4">
          <li>
            <Link to="/" className="hover:bg-gray-700 px-3 py-2 rounded">
              {t("home.title")}
            </Link>
          </li>
          <li>
            <Link
              to="/Invoices"
              className="hover:bg-gray-700 px-3 py-2 rounded"
            >
              {t("invoices.title")}
            </Link>
          </li>
          <li>
            <Link
              to="/Products"
              className="hover:bg-gray-700 px-3 py-2 rounded"
            >
              {t("products.title")}
            </Link>
          </li>
          <li>
            <Link
              to="/Customers"
              className="hover:bg-gray-700 px-3 py-2 rounded"
            >
              {t("customers.title")}
            </Link>
          </li>
          <li>
            <Link to="/Reports" className="hover:bg-gray-700 px-3 py-2 rounded">
              {t("reports.title")}
            </Link>
          </li>
          <li>
            <Link to="/Users" className="hover:bg-gray-700 px-3 py-2 rounded">
              {t("users.title")}
            </Link>
          </li>

          <li>
            <Link to="/About" className="hover:bg-gray-700 px-3 py-2 rounded">
              {t("about.title")}
            </Link>
          </li>
        </ul>
        <UserProfile />
      </div>
    </nav>
  );
};

export default Navbar;
